.circle-button {
    @include circle-button();
}

// символы-иконки css требуют подгонки
.circle-button-play {
    @include ball (8rem, $blue);
    position: relative;
    z-index: 10;

    &:hover {
        cursor: pointer;
        filter: hue-rotate(75deg);
    }

    &::before {
        content: '\f04b';
        font-family: 'Font Awesome 6 Free';
        font-style: normal;
        font-weight: 700;
        position: absolute;
        color: $white;
        font-size: 4.0rem;
        top: 50%;
        left: 50%;
        transform:  translate(-37%, -48%);
    }
}